import { homepage } from '@/api/user.js'
export default {
	data() {
		return {
			bannerList: [],
			courseTypes: [],
			hots: [],
			lecturers: [],
			recommends: [],
			swiperIndex: 0
		};
	},
	onLoad() {
		homepage().then(res=>{
			console.log(res)
			if(res.code === 200) {
				let data = res.data;
				this.bannerList = data.banners;
				this.courseTypes = data.courseTypes;
				this.hots = data.hots;
				this.lecturers = data.lecturers;
				this.recommends = data.recommends;
			}
		})
	},
	methods:{
		swiperChange(e){
			let i = e.detail.current;
			this.swiperIndex = i;
		}
	}
}
